




































import { Component, Vue, Prop } from 'vue-property-decorator';

@Component({})
export default class placeHolder extends Vue {
    @Prop() private loading!: boolean;

    data() {
        return {
            boilerplate: false,
            tile: false,
            type: 'image',
            type2: 'heading',
            type3: 'paragraph',
            types: [],
            dragOptions: {
                animation: 200,
                group: 'description',
                disabled: false,
                ghostClass: 'ghost',
            },
        };
    }
}
